import React from 'react';
import { AvatarWrap, NavBar, NavBarItem, NavBarInner, RouteContent, AvatarImgWrap, NavBarItemText, NameAndCarrierWrap, NameText, CarrierText } from '../Styled';
import { ExperienceIcon, BiographyIcon, ContactMeIcon } from '../Icons';
import { withRouter } from 'react-router-dom';
import { matchPath } from 'react-router';
import '../components/swiper.min.css';
import '../index.sass';

const Layout = ({location, children}) =>  {
		return (
			<>
				<AvatarWrap>
					<AvatarImgWrap />
					<NameAndCarrierWrap>
						<NameText>
							Armin Yahya
					</NameText>
						<CarrierText>
							Frontend Developer
					</CarrierText>
					</NameAndCarrierWrap>
				</AvatarWrap>
				<NavBar>
					<NavBarInner>
						<NavBarItem to="/ContactMe" className={!!location.pathname.includes('ContactMe') ? 'js-active' : ''}>
							<ContactMeIcon />
							<NavBarItemText>Contact Me</NavBarItemText>
						</NavBarItem>
						<NavBarItem to="/Biography" className={!!location.pathname.includes('Biography') ? 'js-active' : ''}>
							<BiographyIcon />
							<NavBarItemText>Biography</NavBarItemText>
						</NavBarItem>
						<NavBarItem to="/Experience" className={!!location.pathname.includes('Experience')  || location.pathname === '/cv/'  ? 'js-active' : ''}>
							<ExperienceIcon />
							<NavBarItemText>Experience</NavBarItemText>
						</NavBarItem>
					</NavBarInner>
				</NavBar>
				<RouteContent>{children}</RouteContent>
			</>
		);
	}

export default Layout;
